<template>
  <div
    class="lineChart"
    :ref="refName"
    :style="{ width: width, height: height }"
  ></div>
</template>
<script>
// import { fontSize } from '@/utils/common';
export default {
  name: 'LineChart',
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '100%'
    },
    refName: {
      type: String,
      default: 'chart'
    },
    chartName: {
      type: String,
      default: '计划产量'
    },
    chartData: {
      type: Object,
      default: () => ({})
    }
  },

  data() {
    return {
      data: [],
      loading: false,
      chart: null
    };
  },
  watch: {
    chartData: {
      handler() {
        this.drawChart();
      },
      deep: true
    }
  },
  methods: {
    drawChart() {
      const option = {
        title: [
          {
            text: this.chartData.titleVal,
            x: 'center',
            top: '55%',
            textStyle: {
              color: '#16CEB9',
              fontSize: '18',
              fontWeight: '300'
            }
          },
          {
            text: this.chartName,
            x: 'center',
            top: '35%',
            textStyle: {
              fontSize: '18',
              color: '#FFFFFF',
              // fontFamily: 'DINAlternate-Bold, DINAlternate',
              foontWeight: '600'
            }
          }
        ],
        polar: {
          radius: ['70%', '85%'],
          center: ['50%', '50%']
        },
        angleAxis: {
          max: this.chartData.full,
          show: false
        },
        radiusAxis: {
          type: 'category',
          show: true,
          axisLabel: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: false
          }
        },
        series: [
          {
            name: '',
            type: 'bar',
            roundCap: true,
            barWidth: 90,
            showBackground: true,
            backgroundStyle: {
              color: 'rgba(66, 66, 66, .2)'
            },
            data: [this.chartData.value],
            coordinateSystem: 'polar',

            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [
                  {
                    offset: 0,
                    color: '#16CEB9'
                  },
                  {
                    offset: 1,
                    color: '#6648FF'
                  }
                ])
              }
            }
          }
        ]
      };
      this.chart.setOption(option);
    },
    init() {}
  },
  mounted() {
    this.chart = this.$echarts.init(this.$refs[this.refName]);
    setTimeout(() => {
      this.drawChart();
    }, 500);
  }
};
</script>
<style lang="less" scoped></style>
