<template>
  <div class="kanban">
    <div class="titleHead">
      <p id="pHead">{{ currentDeviceName }}</p>
      <p id="time" style="margin-right: 180px;" >当前上机人员：{{ upDevUser }}</p>
      <el-button class="reportBtn" @click="toReport">报表</el-button>
      <el-button class="backBtn" @click="$router.back()">返回</el-button>
    </div>
    <div class="kanbanContent">
      <div class="leftContent">
        <div class="left1">
          <card-module class="cardModule"  title="状态时间图" cardBg="cardBg_large" titleBg="cardTitle_large" titleWidth="70%"
            width="100%" height="100%" :isExport="false">
            <!-- <div class="right3title">状态时间图</div> -->
            <!-- 状态时间图 -->
            <div class="showInfo2">
              <div class="timeEcharts">
                <div class="divL">
                  <div class="runDiv">
                    <div>
                      <p style="cursor: pointer;">{{ EQUIPMENT_LIVE.RUNNING_TIME }}</p>
                      <p>{{ stateData[0].value }}</p>
                    </div>
                  </div>
                  <div class="warnDiv">
                    <div>
                      <p style="cursor: pointer;">{{ EQUIPMENT_LIVE.ALARM_TIME }}</p>
                      <p>{{ stateData[3].value }}</p>
                    </div>
                  </div>
                </div>
                <div class="divC">
                  <p style="color: aqua;">{{ EQUIPMENT_LIVE.STATE_TIME_CHART }}</p>
                  <div class="echartsDiv">
                    <div id="showEhcarts">
                    </div>
                    <!-- <span class="divC_title">{{ $store.state.pageInfo.pageConfigInfo.yieldKey }}</span>
                  <span class="divC_num">{{ cumulativeProduction }}</span> -->
                  </div>
                </div>
                <div class="divR">
                  <div class="freeDiv">
                    <div>
                      <p style="cursor: pointer;">{{ EQUIPMENT_LIVE.FREE_TIME }}</p>
                      <p>{{ stateData[1].value }}</p>
                    </div>
                  </div>
                  <div class="nolineDiv">
                    <div>
                      <p style="cursor: pointer;">{{ EQUIPMENT_LIVE.OFFLINE_TIME }}</p>
                      <p>{{ stateData[2].value }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </card-module>
        </div>
        <div class="left2">
          <div class="left2title">设备效率</div>
          <div class="left2content" id="echarts1" ref="echarts1"></div>
          <div class="line"></div>
        </div>
        <div class="left3">
          <div class="left3title">设备告警</div>
          <div class="left3content">
            <table id="warnTable" >
              <tr v-for="(item, index) in warnList" :key="index" >
                <td><div class="warnRed" ></div></td>
                <td>
                  <p class="active" >{{item.deviceName}}   于  {{item.warningStartTime}} </p>
                  <p class="active"  >报警内容：{{warnFunc(item)}}</p>
                </td>
              </tr>
            </table>
          </div>
          <div class="line"></div>
        </div>
      </div>
      <div class="rightContent">
        <div class="right_top">
          <pie-progress chartName="计划产量" refName="planPro" width="20%" height="80%" :chartData="planProData"></pie-progress>
          <pie-progress chartName="实际产量" refName="actualPro" width="20%" height="80%" :chartData="actualProData"></pie-progress>
          <pie-progress chartName="OEE" refName="oeeChart" width="20%" height="80%" :chartData="oeeChartData"></pie-progress>
        </div>
        <!-- <div class="right1">
          <div class="right1title">设备分布</div>
        </div> -->
        <div class="right2">
          <div class="right2title">宕机时间</div>
          <div class="right2content" id="echarts4" ref="echarts4"></div>
        </div>
        <div class="right2">
          <div class="right2title">良品率</div>
          <div class="right2content" id="echarts5" ref="echarts5"></div>
        </div>
        <div class="right2">
          <div class="right2title">稼动率</div>
          <div class="right2content" id="echarts6" ref="echarts6"></div>
        </div>
        <div class="right2">
          <div class="right2title">产能</div>
          <div class="right2content" id="echarts7" ref="echarts7"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { parseTime } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import CardModule from '@/components/common/kanban/CardModule';
import PieProgress from '@/components/common/kanban/PieProgress';

// 导入所需接口
import {
  deviceReportGetDeviceDayUtilization,
  deviceEarlyWarningGetDeviceWarnRecord
} from '@/api/kanban';
import { listWarnStatisticsChart, oeeChart } from '@/api/xwReport';
import { GetOneDayDetailStateClock } from '@/api/equipmentstatus';
import {
  STATUS_CODE,
  EQUIPMENT_LIVE,
  TITLE,
  VARIABLE
} from '@/utils/constantsFile';
// 导入通用方法
import {
  // getGroupId,
  // getTodayFirstDay,
  // getTodayLastDay,
  getSearchToday
} from '@/utils/publicFunction2';
import {
  // selectFile,
  getDeviceWorkState
  // REPAIR_FILE,
  // downloadFile,
  // deleteFileById
} from '@/api/andon';
export default {
  name: 'kanban',
  components: {
    CardModule,
    PieProgress
  },
  data() {
    return {
      title: '',
      groupId: 0,
      allId: 0,
      showRun: false,
      timer: null,
      timer4: {},
      time: '',
      warnHtml: '',
      UtilizNum: 0,
      devList: [],
      devLbIndex: 0,
      warnList: [],
      // 设备分布数据
      productTaskData: [],
      // 生产任务 每行的展示的设备数
      rowDeviceNum: 10,
      repairCard: {
        no: 0,
        do: 0,
        off: 0
      },
      maintainCard: {
        no: 0,
        do: 0,
        off: 0
      },
      checkCard: {
        no: 0,
        do: 0,
        off: 0
      },
      current: 1,
      size: 6,
      current_w: 1,
      size_w: 4,
      kanbanConfig: { title: '数字化车间看板', key: '', time: 15000 },
      electricList: [],
      todayElecticeNum: 0,
      EQUIPMENT_LIVE,
      stateData: [
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        },
        {
          name: 0,
          value: EQUIPMENT_LIVE.NOTIME
        }
      ],
      planProData: {
        full: 0,
        value: 0,
        titleVal: 0
      },
      actualProData: {
        full: 0,
        value: 0,
        titleVal: 0
      },
      oeeChartData: {
        full: 100,
        value: 0,
        titleVal: 0
      },
      currentDeviceCode: '',
      currentDeviceName: '',
      upDevUser: ''
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.init();
    });
  },
  created() {
    var that = this;
    window.toDevicePage = that.toDevicePage;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.currentDeviceCode = this.getUrlKeyMain('deviceCode', window.location.href);
    this.currentDeviceName = this.getUrlKeyMain('deviceName', window.location.href);
    console.log(this.getUrlKeyMain('tvNo', window.location.href));
    if (this.getUrlKeyMain('tvNo', window.location.href) !== null) {
      this.title = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].title;
      this.groupId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].id;
      this.allId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].allId;
      this.showRun = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].showRun;
    } else {
      this.title = that.kanbanConfig.title;
    }

    // this.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];

    // setInterval(() => {
    //   that.tick();
    // }, 1000);
    this.getDataTime();

    this.timer = setInterval(() => {
      that.init();
    }, that.kanbanConfig.time || 15000);
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  },
  methods: {
    init() {
      this.getDeviceTimeRecentDayUtilization(); // 设备效率
      this.getState(); // 状态时间图
      this.showEcharts4Data(); // 宕机时间
      this.showEcharts6Data(); // 稼动率、良率、产能
      this.getPlanActualData(); // 当天计划、实际产量

      this.showWarn(); // 设备预警
      this.getOneDev();
    },
    getOneDev() {
      getDeviceWorkState({
        deviceCode: this.currentDeviceCode
      }, {})
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            if (res.rows.realName) {
              this.upDevUser = res.rows.realName;
            } else {
              this.upDevUser = '无';
            }
          } else {
            showWarningMessage(res.message);
          }
          this.dialogVisible = false;
        });
    },
    toReport() {
      this.$router.push({ path: '/xwreport/capacity', query: { deviceCode: this.currentDeviceCode } });
    },
    warnFunc(param) {
      if (param.warnName === '报警信息') {
        return param.warnValue;
      } else {
        return param.warnName;
      }
    },
    async getDeviceTimeRecentDayUtilization() {
      try {
        var condtion = {
          conditionJson1: [
            {
              column: 'deviceCode',
              operator: 1,
              ptype: 'string',
              type: 'eq',
              value: this.currentDeviceCode
            }
          ]
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        const res = await deviceReportGetDeviceDayUtilization({
          startTime: getSearchToday(7) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59'
        },
        condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var sbxl = [];
          var pjxl = [];
          var time = [];
          var all = [];
          var numAll = 0;
          for (var i = 0; i < res.rows.length; i++) {
            // pjxl.push(Number((data.dataset * 100).toFixed(2)));
            sbxl.push(Number((res.rows[i].value * 100).toFixed(2)));
            time.push(this.formatDateThree(res.rows[i].time));
            all.push(Number((res.rows[i].value * 100).toFixed(2)));
            numAll += res.rows[i].value;
          }
          var avg = ((numAll / all.length) * 100).toFixed(2);
          for (var x = 0; x < all.length; x++) {
            pjxl.push(Number(avg));
          }
          console.log('大家好', pjxl);
          this.showEcharts(sbxl, pjxl, time, Math.min.apply(null, all) - 5, Math.max.apply(null, all) + 5, this.currentDeviceName);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async showEcharts4Data() {
      try {
        const res = await listWarnStatisticsChart({
          startTime: getSearchToday(0) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59',
          timeKeyEnum: 'LESSON',
          deviceCode: this.currentDeviceCode
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          console.log('OEE', res);
          var lpl = [];
          // var jdl = [];
          // var oee = [];
          var time = [];
          for (var i = 0; i < res.rows.length; i++) {
            lpl.push(Number(res.rows[i].radio.toFixed(0)));
            // jdl.push(Number(res.rows[i].availability.toFixed(2)));
            // oee.push(Number((res.rows[i].availability * res.rows[i].yield / 100).toFixed(2)));
            time.push(res.rows[i].key);
          }
          this.showEcharts4(lpl, time);
        }
      } catch (error) {
        console.log('OEE', error);
        console.log(error);
      }
    },
    /**
    * @description: 获取设备24小时状态
    * @param obj ztree对象
    * @return: 无
    *@author: CH
    */
    async getState() {
      try {
        // this.tableLoadings = true;
        const res = await GetOneDayDetailStateClock({
          deviceCode: this.currentDeviceCode,
          time: parseTime(new Date(), '{y}-{m}-{d} {h}:{i}:{s}')
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // this.showData = res.rows;
          if (res.rows.cumulativeProduction) {
            // this.cumulativeProduction = res.rows.cumulativeProduction;
            if (getSearchToday(0) === this.stateDataTime) {
              if (res.rows.currentShiftName) {
                this.cumulativeProduction = res.rows.currentValue;
              } else {
                this.cumulativeProduction = res.rows.cumulativeProduction;
              }
            } else {
              this.cumulativeProduction = res.rows.cumulativeProduction;
            }
          } else {
            this.cumulativeProduction = 0;
          }
          // var color = [];
          var echartsData = [];
          this.timeList = res.rows.timeList;
          for (var i in res.rows.timeList) {
            // echartsData[i].name = res.rows[i].state;
            // echartsData[i].value = res.rows[i].time;
            if (res.rows.runTimeSum) {
              this.stateData[0].value = this.timeStamp(res.rows.runTimeSum);
            } else {
              this.stateData[0].value = '0分钟';
            }
            if (res.rows.idleTimeSum) {
              this.stateData[1].value = this.timeStamp(res.rows.idleTimeSum);
            } else {
              this.stateData[1].value = '0分钟';
            }
            if (res.rows.offlineTimeSum) {
              this.stateData[2].value = this.timeStamp(res.rows.offlineTimeSum);
            } else {
              this.stateData[2].value = '0分钟';
            }
            if (res.rows.warnTimeSum) {
              this.stateData[3].value = this.timeStamp(res.rows.warnTimeSum);
            } else {
              this.stateData[3].value = '0分钟';
            }
            if (res.rows.stopTimeSum) {
              this.stateData[4].value = this.timeStamp(res.rows.stopTimeSum);
            } else {
              this.stateData[4].value = '0分钟';
            }
            if (res.rows.timeList[i].state === 0) {
              // color.push('#FFFFFF');
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#FFFFFF' }
              });
            }
            if (res.rows.timeList[i].state === 1) {
              // this.stateData[0].name = res.rows[i].state;
              // this.stateData[0].value = this.timeStamp(res.rows[i].time);
              // color.push('#027cf8');
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#027cf8' }
              });
            }
            if (res.rows.timeList[i].state === 2) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#fed016' }
              });
            }
            if (res.rows.timeList[i].state === 3) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#5b6069' }
              });
            }
            if (res.rows.timeList[i].state === 4) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#c84747' }
              });
            }
            if (res.rows.timeList[i].state === 5) {
              echartsData.push({
                name: res.rows.timeList[i].state,
                value: res.rows.timeList[i].time,
                itemStyle: { color: '#ff6600' }
              });
            }
          }
          this.showEcharts_State(echartsData);
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
        * @description: 处理时间显示
        * @param obj ztree对象
        * @return: 无
        *@author: CH
        */
    timeStamp(StatusMinute) {
      var day = parseInt(StatusMinute / 60 / 24);
      var hour = parseInt((StatusMinute / 60) % 24);
      var min = parseInt(StatusMinute % 60);
      var value = '';
      if (day === 1) {
        value = '24' + VARIABLE.HOUR_NAME + '0' + VARIABLE.MINUTE_NAME;
      }
      if (hour > 0) {
        value += hour + VARIABLE.HOUR_NAME;
      }
      if (min > 0) {
        value += parseFloat(min) + VARIABLE.MINUTE_NAME;
      }

      if (StatusMinute % 1 > 0) {
        value += parseInt((StatusMinute % 1) * 60) + '' + '秒';
      }

      return value;
    },
    /**
    * @description:展示24小时状态图
    * @param {type}
    * @return:
    * @author: CH
    */
    showEcharts_State(echartsData) {
      var myChart = this.$echarts.init(document.getElementById('showEhcarts'));
      // 绘制图表
      myChart.setOption({
        series: [
          {
            name: '',
            type: 'pie',
            radius: ['60%', '80%'],
            hoverAnimation: false,
            // avoidLabelOverlap: false,
            label: {
              normal: {
                show: false,
                position: 'center'
              },
              emphasis: {
                show: false
              }
            },
            labelLine: {
              normal: {
                show: false
              }
            },
            // color: color,
            data: echartsData
          }
        ]
      });
    },
    oeeFunc(lpl, jdl, ct, cl, rt) {
      return (lpl * jdl * ((ct * cl) / (rt * 60)) / 100).toFixed(2);
    },
    // 获取当天的计划产量、实际产量、OEE
    async getPlanActualData() {
      try {
        const res = await oeeChart({
          startTime: getSearchToday(0) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59',
          timeKeyEnum: 'DAY',
          queryRunTime: true,
          deviceCode: this.currentDeviceCode
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const data = res.rows[0];
          for (const key in this.planProData) {
            this.planProData[key] = data.planQty;
          }
          for (const key in this.actualProData) {
            this.actualProData[key] = data.actualQty;
          }
          const lpl = data.yield?.toFixed(2) || 0;
          const jdl = data.availability?.toFixed(2) || 0;
          this.oeeChartData.value = this.oeeFunc(lpl, jdl, data.planCt, data.actualQty, data.runTime);
          this.oeeChartData.titleVal = this.oeeFunc(lpl, jdl, data.planCt, data.actualQty, data.runTime) + '%';
          console.log(res);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async showEcharts6Data() {
      try {
        const res = await oeeChart({
          startTime: getSearchToday(0) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59',
          timeKeyEnum: 'LESSON',
          deviceCode: this.currentDeviceCode
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var jdl = [];
          var cl = [];
          var jhcl = [];
          var lpl = [];
          var jhlpl = [];
          // var jdl = [];
          // var oee = [];
          var time = [];
          for (var i = 0; i < res.rows.length; i++) {
            if (res.rows[i].availability) {
              jdl.push(Number(res.rows[i].availability.toFixed(2)));
            } else {
              jdl.push(0);
            }

            if (res.rows[i].yield) {
              cl.push(Number(res.rows[i].actualQty.toFixed(0)));
            } else {
              cl.push(0);
            }
            if (res.rows[i].planQty) {
              jhcl.push(Number(res.rows[i].planQty.toFixed(0)));
            } else {
              jhcl.push(0);
            }

            if (res.rows[i].yield) {
              lpl.push(Number(res.rows[i].yield.toFixed(2)));
            } else {
              lpl.push(0);
            }
            if (res.rows[i].planYield) {
              jhlpl.push(Number(res.rows[i].planYield.toFixed(2)));
            } else {
              jhlpl.push(0);
            }
            // jhlpl.push(Number(res.rows[i].planYield.toFixed(2)));

            // jdl.push(Number(res.rows[i].availability.toFixed(2)));
            // oee.push(Number((res.rows[i].availability * res.rows[i].yield / 100).toFixed(2)));
            time.push(res.rows[i].key);
          }
          this.showEcharts6(jdl, time);
          this.showEcharts7(cl, jhcl, time);
          this.showEcharts5(lpl, jhlpl, time);
        }
      } catch (error) {
        console.log('OEE', error);
        console.log(error);
      }
    },
    toDevicePage(data) {
      this.$router.push({ path: '/xwreport/capacity', query: { deviceCode: data } });
    },
    async showWarn() {
      if (window.innerWidth <= 1024) {
        this.size_w = 3;
      }
      var condtion = {
        current: this.current_w,
        size: this.size_w,
        conditionJson1: [
          {
            column: 'deviceCode',
            operator: 1,
            ptype: 'string',
            type: 'eq',
            value: this.currentDeviceCode
          }
        ]
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      try {
        const res = await deviceEarlyWarningGetDeviceWarnRecord({
          processStateEnum: 'UN_PROCESS'
        }, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (this.current_w * this.size_w >= res.total) {
            this.current_w = 1;
          } else {
            this.current_w++;
          }

          this.warnList = res.rows;
          // var html = '';
          // for (var i = 0; i < res.rows.length; i++) {
          //   var json = res.rows[i];
          //   html += '<tr>';
          //   html += '  <td><div class="warnRed" ></div></td>';
          //   html += '  <td>';
          //   html += ' <p class="active" >' + json.productionLine + '   ' + json.deviceName + '  于' + json.warningStartTime + '  告警</p>';
          //   html += '  </td>';
          //   html += '</tr>';
          // }
          // this.warnHtml = html;
        }
      } catch (error) {
        console.log(error);
      }
    },
    showEcharts(sbxl, pjxl, time, min, max, devName) {
      var color = [
        '#36CE9E',
        '#0090FF',
        '#FFC005',
        '#FF515A',
        '#8B5CFF',
